import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

const Wrapper = styled.div`
  height: 100vh;
  width: 100vw;

  margin: 0;
  padding: 0;
`;

const Container = styled.div`
  display: inline-block;

  position: absolute;

  left: ${props => props.containerPosition.left};
  top: ${props => props.containerPosition.top};
`;

const Table = styled.table`
  border: 1px solid #48aae6;
  cursor: grab;
`;

const Cell = styled.td`
  height: ${props => props.cellSize}px;
  width: ${props => props.cellSize}px;

  padding: 0;

  background-color: #48aae6;
`;

const Button = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;

  height: ${props => props.cellSize}px;
  width: ${props => props.cellSize}px;
  margin: 2px;

  color: #ffffff;

  font-size: ${props => props.cellSize / 2}px;
  font-weight: bold;
  text-align: center;

  opacity: 1;

  transition-duration: 0.6s;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
    transition-duration: 0.5s;
  }
`;

const AddButtons = styled(Button)`
  background-color: #f3a500;
`;

const RemoveButtons = styled(Button)`
  display: block;
  visibility: hidden;

  background-color: #b20000;

  line-height: ${props => props.cellSize - props.cellSize / 5}px;
`;

const AddRowButton = styled(AddButtons)`
  top: 100%;
  left: 1px;
`;

const AddColumnButton = styled(AddButtons)`
  top: 1px;
  left: 100%;
`;

const RemoveRowButton = styled(RemoveButtons)`
  display: ${props =>
    props.removeRowButtonDisplay === true ? "block" : "none"};
  visibility: ${props =>
    props.buttonsVisible === true ? "visible" : "hidden"};
  opacity: ${props => (props.buttonsVisible === true ? 1 : 0)};

  top: ${props => props.removeRowButtonTop}px;
  right: 100%;

  margin: 1px 2px;
`;

const RemoveColumnButton = styled(RemoveButtons)`
  display: ${props =>
    props.removeColumnButtonDisplay === true ? "block" : "none"};
  visibility: ${props =>
    props.buttonsVisible === true ? "visible" : "hidden"};
  opacity: ${props => (props.buttonsVisible === true ? 1 : 0)};

  bottom: 100%;
  left: ${props => props.removeColumnButtonLeft}px;

  margin: 2px 1px;
`;

export default class Square extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      square: [],

      rows: [],
      columns: [],

      rowKey: 0,
      cellKey: 0,

      containerPosition: {
        left: `${this.props.cellSize * 2}px`,
        top: `${this.props.cellSize * 2}px`
      },

      currentCellIndex: 0,
      currentRowIndex: 0,

      buttonsVisible: false,
      removeRowButtonDisplay: true,
      removeColumnButtonDisplay: true,

      removeRowButtonTop: 0,
      removeColumnButtonLeft: 0,

      dragging: false
    };

    this.rowKey = 0;
    this.cellKey = 0;

    this.offsetX = 0;
    this.offsetY = 0;
  }

  componentDidMount() {
    this.createSquare();
    this.container = document.querySelector("#container");
  }

  createSquare = () => {
    const { rows, columns } = this.state;
    const cloneRows = [...rows];
    const cloneColumns = [...columns];

    const { initialHeight, initialWidth } = this.props;

    for (let cells = 0; cells < initialWidth; cells++) {
      cloneColumns[cells] = {
        key: this.cellKey++
      };
    }

    for (let rows = 0; rows < initialHeight; rows++) {
      cloneRows[rows] = {
        key: this.rowKey++,
        columns: cloneColumns
      };
    }

    this.setState({
      rows: cloneRows,
      columns: cloneColumns
    });
  };

  onDragStart = ({ clientX, clientY }) => {
    this.offsetX = clientX - this.container.getBoundingClientRect().left;
    this.offsetY = clientY - this.container.getBoundingClientRect().top;

    this.setState({
      dragging: true
    });
  };

  onDragging = ({ pageX, pageY }) => {
    const { dragging } = this.state;

    if (dragging) {
      this.container.style.left = pageX - this.offsetX + "px";
      this.container.style.top = pageY - this.offsetY + "px";
    }
  };

  onDragEnd = () => {
    const { containerPosition } = this.state;
    const cloneContainerPosition = { ...containerPosition };

    cloneContainerPosition.left = this.container.style.left;
    cloneContainerPosition.top = this.container.style.top;

    this.setState({
      dragging: false,
      containerPosition: cloneContainerPosition
    });

    this.container.removeAttribute("style");
  };

  movingButtons = ({ target }) => {
    let {
      currentRowIndex,
      currentCellIndex,
      removeColumnButtonLeft,
      removeRowButtonTop
    } = this.state;

    if (target.tagName === "TD") {
      removeColumnButtonLeft = target.offsetLeft;
      removeRowButtonTop = target.offsetTop;
      currentCellIndex = target.cellIndex;
      currentRowIndex = target.parentNode.rowIndex;
    }

    this.setState({
      currentRowIndex,
      currentCellIndex,
      removeColumnButtonLeft,
      removeRowButtonTop
    });
  };

  createColumn = () => {
    const { columns } = this.state;
    const cloneColumns = [...columns, { key: this.cellKey++ }];

    this.setState({
      columns: cloneColumns
    });
  };

  createRow = () => {
    const { rows, columns } = this.state;
    const cloneColumns = [...columns];
    const cloneRows = [...rows, { key: this.rowKey++, columns: cloneColumns }];

    this.setState({
      rows: cloneRows
    });
  };

  deleteColumn = () => {
    const { cellSize } = this.props;
    const { rows, columns } = this.state;
    let { currentCellIndex, removeColumnButtonLeft } = this.state;

    const cloneRows = [...rows];
    const cloneColumns = [...columns];

    const columnsLength = cloneColumns.length;
    const lastCellIndex = columnsLength - 1;

    if (columnsLength > 1) {
      cloneColumns.splice(currentCellIndex, 1);
    }

    if (currentCellIndex === lastCellIndex) {
      /* In this formula "2" - the padding of each cell, for the correct movement
      of the button should be considered when calculating */
      removeColumnButtonLeft =
        cellSize * (currentCellIndex - 1) + 2 * currentCellIndex;

      currentCellIndex--;
    }

    if (lastCellIndex <= 1) {
      this.setState({
        removeColumnButtonDisplay: false
      });
    }

    this.setState({
      rows: cloneRows,
      columns: cloneColumns,
      removeColumnButtonLeft,
      currentCellIndex
    });
  };

  deleteRow = () => {
    const { cellSize } = this.props;
    const { rows } = this.state;
    let { currentRowIndex, removeRowButtonTop } = this.state;

    const cloneRows = [...rows];

    const rowsLength = cloneRows.length;
    const lastRowIndex = rowsLength - 1;

    if (rowsLength > 1) {
      cloneRows.splice(currentRowIndex, 1);
    }

    if (currentRowIndex === lastRowIndex) {
      /* In this formula "2" - the padding of each cell, for the correct movement
    of the button should be considered when calculating */
      removeRowButtonTop =
        cellSize * (currentRowIndex - 1) + 2 * currentRowIndex;

      currentRowIndex--;
    }

    if (lastRowIndex <= 1) {
      this.setState({
        removeRowButtonDisplay: false
      });
    }

    this.setState({
      rows: cloneRows,
      removeRowButtonTop,
      currentRowIndex
    });
  };

  showButtons = () => {
    const { rows, columns } = this.state;

    if (rows.length > 1) {
      this.setState({
        buttonsVisible: true,
        removeRowButtonDisplay: true
      });
    }

    if (columns.length > 1) {
      this.setState({
        buttonsVisible: true,
        removeColumnButtonDisplay: true
      });
    }
  };

  hideButtons = () => {
    this.setState({
      buttonsVisible: false
    });
  };

  render() {
    const { cellSize } = this.props;

    const {
      rows,
      columns,
      buttonsVisible,
      removeRowButtonDisplay,
      removeColumnButtonDisplay,
      removeRowButtonTop,
      removeColumnButtonLeft,
      containerPosition
    } = this.state;

    return (
      <Wrapper onMouseMove={this.onDragging} onMouseUp={this.onDragEnd}>
        <Container
          id={"container"}
          containerPosition={containerPosition}
          cellSize={cellSize}
          onMouseOver={this.movingButtons}
        >
          <div onMouseEnter={this.showButtons} onMouseLeave={this.hideButtons}>
            <Table onMouseDown={this.onDragStart} onDragStart={() => false}>
              <tbody>
                {rows.map(row => (
                  <tr key={`row-${row.key}`}>
                    {columns.map(cell => (
                      <Cell key={`cell-${cell.key}`} cellSize={cellSize} />
                    ))}
                  </tr>
                ))}
              </tbody>
            </Table>

            <RemoveRowButton
              onClick={this.deleteRow}
              cellSize={cellSize}
              buttonsVisible={buttonsVisible}
              removeRowButtonDisplay={removeRowButtonDisplay}
              removeRowButtonTop={removeRowButtonTop}
            >
              -
            </RemoveRowButton>
            <RemoveColumnButton
              onClick={this.deleteColumn}
              cellSize={cellSize}
              buttonsVisible={buttonsVisible}
              removeColumnButtonDisplay={removeColumnButtonDisplay}
              removeColumnButtonLeft={removeColumnButtonLeft}
            >
              -
            </RemoveColumnButton>
          </div>
          <AddRowButton cellSize={cellSize} onClick={this.createRow}>
            +
          </AddRowButton>
          <AddColumnButton cellSize={cellSize} onClick={this.createColumn}>
            +
          </AddColumnButton>
        </Container>
      </Wrapper>
    );
  }
}

Square.defaultProps = {
  cellSize: 50,
  initialHeight: 4,
  initialWidth: 4
};

Square.propTypes = {
  cellSize: PropTypes.number.isRequired,
  initialHeight: PropTypes.number.isRequired,
  initialWidth: PropTypes.number.isRequired
};
